<script>
  import { isEmpty } from 'lodash'
  import { prop } from 'ramda'

  import FileComponent from '@/components/file-component.vue'
  import PositionsQuery from './positions.gql'
  import addTags from './add-tags/main'
  import submitForm from './submit-form'
  import * as FormComponents from '@/domains/topics/view/form-components'

  export default {
    name: 'create-topic',
    components: {
      addTags,
      FileComponent,
      ...FormComponents
    },
    data () {
      return {
        positions: [],
        id_position: '',
        content: '',
        title: '',
        selected: [],
        isActive: false,
        selectedTags: [],
        file: {},
        hasImageError: null
      }
    },
    apollo: {
      positions () {
        return {
          query: PositionsQuery
        }
      }
    },
    computed: {
      hasSelectedTags () {
        return !isEmpty(this.selectedTags)
      },
      isFormValid () {
        return !isEmpty(this.title) && !isEmpty(this.content) && !isEmpty(this.selectedTags)
      },
      tags () {
        return this.selectedTags.map(prop('uid'))
      },
      topic () {
        return {
          title: this.title,
          content: this.content,
          id_position: this.id_position,
          tag: this.tags
        }
      }
    },
    methods: {
      submit () {
        const loading = this.$loading.open()
        submitForm(this, this.file, this.topic)
          .then(user => {
            loading.close()
            this.reset()
          })
          .catch(console.error)
      },
      getSelects (tags) {
        this.selectedTags = [ ...tags ]
      },
      reset () {
        this.id_position = ''
        this.content = ''
        this.title = ''
        this.selectedTags = []
        this.files = []
      },
      onLoadFile (file) {
        this.file = file
      },
      onLoadImageError (hasImageError) {
        this.hasImageError = hasImageError
      }
    }
  }
</script>

<template lang="html">
  <div class="create-topic">
    <h1 class="title has-text-centered"> Criar tópico </h1>
    <hr>

    <div class="form">
      <form-title :title.sync="title" />

      <form-content :content.sync="content" />

      <file-component
        label="Selecione a imagem"
        message="Imagem requerida: 1000px por 300px"
        :widthAccepted="1000"
        :heightAccepted="300"
        :willCheck="true"
        @load-file="onLoadFile"
        @load-errors="onLoadImageError" />

      <b-field
        label="Tags selecionadas"
        v-if="hasSelectedTags">
        <b-taglist>
          <b-tag
            v-for="tag in selectedTags" :key="tag.uid"
            type="is-primary"
            size="is-medium"> {{ tag.label }} </b-tag>
        </b-taglist>
      </b-field>

      <button
        class="button is-medium"
        @click="isActive = true"> Selecionar tags </button>

      <hr>

      <add-tags
        :isActive.sync="isActive"
        @get-select="getSelects" />

      <button
        class="button is-success is-medium"
        :disabled="!isFormValid"
        @click="submit"> Criar tópico </button>
    </div>
  </div>
</template>

<style lang="scss" scoped>
  .create-topic {
    padding: 20px;
  }

  hr,
  .form {
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
  }

  .upload.control {
    display: block;
    margin-bottom: 10px;
  }

  .tags {
    margin-bottom: 0;
  }

  .app-form {
    max-width: 600px;
    margin: 0 auto;
  }

  .label {
    display: block;
  }
</style>



